.footer{
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap:50px;
}

.footer-logo{
    display:flex;
    align-items: center;
    gap:20px;
}
.footer-logo p{
    color:grey;
    font-size:46px;
    font-weight:700;
}
.footer-links{
    display:flex;
    list-style:none;
    gap:50px;
    color:black;
    font-size:20px;
}

.footer-links li{
    cursor:pointer;
}
.footer-social-icon{
    display:flex;
    gap:20px;
}
.footer-icons-container{
    padding:10px;
    padding-bottom:6px;
    background:white;
    border:1 px solid;
}
.footer-copyright{
    display:flex;
    flex-direction:column;
    align-items:center;
    gap:30px;
    width:100%;
    margin-bottom:30px;
    color:black;
    font-size:20px;
}

.footer-copyright hr{
    width:80%;
    border:none;
    border-radius:10px;
    height:3px;
    background:white;
}
