.descripcion{
margin: 120px 170px;
}
.descripcion-nav{
    display:flex;
}
.descripcion-navbox{
display:flex;
align-items: center;
justify-content: center;
font-size:16px;
font-weight:600;
width:171px;
height:70px;
border:1px solid #d0d0d0;
}
.descripcion-navbox-fade{
    background:white;
    color:#555;
}
       
.descripcionbox-descripcion{
    display:flex;
    flex-direction:column;
    gap:25px;
    border:1px solid white;
    padding:48px;
    padding-bottom:70px;
}