.offers{
    width:65%;
    height: 60vh;
    display:flex;
    margin:auto;
    padding:0px 140px;
    margin-bottom:150px;
    background: linear-gradient(180deg,#fde1ff 0%,#e1ffea22 60%);
}

.offers-left{
    flex:1;
    display:flex;
    flex-direction:column;
    justify-content:center;  
}

.offers-left h1{
    color: black;
    font-size: 80px;
    font-weight:600;
}

offers-left p{
    color:black;
    font-size:22px;
    font-weight:600;
}
offers-left button{
    width: 28px;
    height:70px;
    border-radius:35px;
    background-color:red;
    color:white;
    font-size:22px;
    font-weight:500;
    margin-top:30px;
    cursor:pointer;
}
.offers-right{
    flex:1;
    display:flex;
    align-items:center;
    justify-content: center;
    padding-top:0%;
}
